import React from 'react';
import Sound from 'react-sound';

function control(text, clickHandler) {
  const onClick = ev => {
    ev.preventDefault();
    clickHandler();
  };
  return (
    <li>
      <a href="#" onClick={onClick}>
        {text}
      </a>
    </li>
  );
}

const numberFormat = new Intl.NumberFormat([], { minimumFractionDigits: 2 });

export default class PlayerControls extends React.Component {
  render() {
    return <div>{this.renderControls()}</div>;
  }

  renderControls() {
    const controls = {
      play: this.props.playStatus === Sound.status.STOPPED,
      stop: this.props.playStatus !== Sound.status.STOPPED,
      pause: this.props.playStatus === Sound.status.PLAYING,
      resume: this.props.playStatus === Sound.status.PAUSED
    };

    return (
      <div>
        {/* Volume:
        <button onClick={this.props.onVolumeDown}>-</button>
        <button onClick={this.props.onVolumeUp}>+</button> */}
        
        <a href="#" target="_blank" class="neoh_fn_button">
			<span class="icon">
            <svg class="fn__svg" id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><title>pause-glyph</title>
            <path d="M395,512a73.14,73.14,0,0,1-73.14-73.14V73.14a73.14,73.14,0,1,1,146.29,0V438.86A73.14,73.14,0,0,1,395,512Z" fill="#434040"/><path d="M117,512a73.14,73.14,0,0,1-73.14-73.14V73.14a73.14,73.14,0,1,1,146.29,0V438.86A73.14,73.14,0,0,1,117,512Z" fill="#434040"/></svg>
			</span>
			<span class="text">Pause</span>
		</a>
        <a href="#" target="_blank" class="neoh_fn_button">
			<span class="icon">
            {/* <svg class="fn__svg" height="16px" id="Layer_1" style="enable-background:new 0 0 16 16;" version="1.1" viewBox="0 0 16 16" width="16px" xmlns="http://www.w3.org/2000/svg">
                <path d="M1,14c0,0.547,0.461,1,1,1c0.336,0,0.672-0.227,1-0.375L14.258,9C14.531,8.867,15,8.594,15,8s-0.469-0.867-0.742-1L3,1.375  C2.672,1.227,2.336,1,2,1C1.461,1,1,1.453,1,2V14z"/></svg> */}
            <svg class="fn__svg" id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><title>play-glyph</title><path d="M60.54,512c-17.06,0-30.43-13.86-30.43-31.56V31.55C30.12,13.86,43.48,0,60.55,0A32.94,32.94,0,0,1,77,4.52L465.7,229c10.13,5.85,16.18,16,16.18,27s-6,21.2-16.18,27L77,507.48A32.92,32.92,0,0,1,60.55,512Z" fill="#434040"/></svg>
			</span>
			<span class="text">Play</span>
		</a>
        
        <ul>
          {controls.play && control('Play', this.props.onPlay)}
          {controls.stop && control('Stop', this.props.onStop)}
          {controls.pause && control('Pause', this.props.onPause)}
          {controls.resume && control('Resume', this.props.onResume)}
        </ul>
        {/* <div>
          Playback Rate:
          <button onClick={this.props.onPlaybackRateDown}>-</button>
          {' '}
          {numberFormat.format(this.props.playbackRate)}
          {' '}
          <button onClick={this.props.onPlaybackRateUp}>+</button>
        </div>
        Loop?:
        <input type="checkbox" checked={this.props.loop} onChange={this.props.onToggleLoop} /> */}
      </div>
    );
  }
}
